import { useState, useEffect} from "react";
import {useNavigate} from 'react-router-dom';
import Axios from 'axios';
import { v4 as uuidv4 } from 'uuid';



export default function Admin(){
    const navegar=useNavigate();
    const [imgPrevia, setImgPrevia]=useState({
        peso:'',
    })
    const [imgNube, setImgNube]=useState("")    
    const [estado, setEstado]=useState(false)
   
    const info =async(ar)=>{
       const imagenEnNube=ar[0];
       console.log(imagenEnNube);
       setImgNube(imagenEnNube);


       await Array.from(ar).forEach( (archivo) => {
            let reader=new FileReader();
            reader.readAsDataURL(archivo);
            reader.onload= async function (){
                const base64= reader.result;
                setImgPrevia({peso:base64})
            }
        });
        
    }
    const [producto, setProducto]=useState({
        productos:"",
        precio:"",
        descripcion:"",
        img:"",
        uuid:"",
        tecnologias:"",
        almacenamiento:"",
        ssl:"", 
        layout:"",
        pasarela_pago:"",
        dominio:"",
        tipo:"",
        herramienta_admin:"",
        html:"",
        css:""
    });
    const handleChange=(e)=>{
        setProducto({...producto, [e.target.name]: e.target.value});
          }
    const subirImgProducto = async(e) => {
        e.preventDefault()
    const data=new FormData();
    data.append("file", imgNube);
    data.append("upload_preset", "david123" )
await Axios.post('https://api.cloudinary.com/v1_1/dwsejzboq/image/upload', data)
.then((respuesta)=>{
    setProducto({...producto, img : respuesta.data.secure_url, uuid:uuidv4()});
});

setEstado(true);
}   
    const subirProducto=async()=>{
        console.log(producto)
    const res=  await Axios.post("/subirProducto", producto,{
          headers:{"Content-Type":"application/json"}
        });
        console.log(res)
        navegar('/')   
    }
    useEffect(() => {
      
        if(estado){
            subirProducto()
            
        }
    },[estado])
      
    return(
        <>
<form onSubmit={subirImgProducto}>
    <h1>producto</h1>
  <input  type="text" placeholder="productos" name='productos' value={producto.productos} onChange={handleChange}/> 
  <input type="number" placeholder="precio" name="precio" value={producto.precio} onChange={handleChange}/>
  <input type="text" placeholder="descripcion" name='descripcion' value={producto.descripcion} onChange={handleChange}/>
  <input  type='file' onChange={(e)=>info(e.target.files)} />
  <h1>Funciones</h1>
  <input type="text" placeholder="tecnologias" name='tecnologias' value={producto.tecnologias} onChange={handleChange}/>
  <input  type="text" placeholder="almacenamiento" name='almacenamiento' value={producto.almacenamiento} onChange={handleChange}/> 
  <input  type="text" placeholder="ssl" name='ssl' value={producto.ssl} onChange={handleChange}/>
  <input  type="text" placeholder="layout" name='layout' value={producto.layout} onChange={handleChange}/> 
  <input  type="text" placeholder="pasarela pago" name='pasarela_pago' value={producto.pasarela_pago} onChange={handleChange}/>
  <input type="text"placeholder="dominio" name='dominio' value={producto.dominio}onChange={handleChange}/>
  <input type="text"placeholder="tipo"name='tipo'value={producto.tipo}onChange={handleChange}/>
  <input type="text"placeholder="herramienta admin"name='herramienta_admin'value={producto.herramienta_admin}onChange={handleChange}/>
  <input type="file"placeholder="file"name='file'value={producto.file}onChange={handleChange}/>
  <h1>Prototipo</h1>          
  <input type="text" placeholder="html"name='html'value={producto.html}onChange={handleChange}/>
  <input type="text" placeholder="caa"name='css'value={producto.css}onChange={handleChange}/>

  <button type='submit' > enviar</button> 
</form>
<p>Vista Previa...</p>
<img src={imgPrevia.peso}/>

        </>
    )
    
    }