import { useState, useEffect} from "react";
import {useNavigate, useParams} from 'react-router-dom';
import Axios from 'axios';
import Dinamic from "./dinamic"
import {Nav} from "./components/routes/header"

export default function Previa() {
  const navegar=useNavigate();
  const parametro = useParams()
  const [files, setfiles] = useState(JSON.parse(localStorage.getItem("files")))
  const [funciones, setFunciones]=useState({});
  const [estado, setEstado]=useState(false)
  const pedirFuciones= async ()=>{
    const res=await Axios.post("/previaProducto", {uuid:parametro.uuid})
    console.log(res.data)
    setFunciones(res.data[0])
    setEstado(true)
  }
  useEffect(() => {    
    pedirFuciones()
  }, []);

   return (
    <> 
    <Nav title1={`Compra  AHORA ! Facil y Rapido`} title2={funciones.producto} descripcion={funciones.descripcion} env1="PHONE" env2="TABLET" env3="PC" />
    <main className="gridPrevia padding30">
    <div className="imgGrande">
    <img  className="imgGrande" src={funciones.img} alt="" />
    </div>
<article className='fondoGrey textType1 textPequeo colorBlack padding20'>
 <h1 className="resaltarText">{funciones.producto}</h1>
 <h1>Precio: {funciones.precio}$</h1>
 <p>Tipo: {funciones.tipo}</p>
 <p>layout: {funciones.layout}</p>
 <p>Dominio: {funciones.dominio}</p>
 <p>Pasarela de pago: {funciones.pasarela_pago}</p>
 <p>Alamacenamientos: {funciones.almacenamiento}</p>
 <p>Certificado ssl: {funciones.ssl}</p>
 <p>Tecnoligias: {funciones.tecnologias}</p> 
</article>

      </main>
      <div className="justificadoColumn rowGap10 marginB30">
<button className="btnType2 alto6" onClick={()=>navegar(`/modificar/${funciones.uuid}`)}>Perzonaliza tu Sitio </button>
<button className="btnType1 alto6"  onClick={()=>navegar(`/checkout/${funciones.tipo}/${funciones.uuid}`)}>comprar Ahora</button>
      </div>
    <div className="alto30 columGap10 justificado slider">
 <img className="imgGrande"  src={funciones.img} />
 <img className="imgGrande" src={funciones.img} />
 <img className="imgGrande" src={funciones.img} />
 <img className="imgGrande" src={funciones.img} />
 <img className="imgGrande" src={funciones.img} />
    </div>


{
  estado ?  <Dinamic html={funciones.html} css={funciones.css} files={files} /> : <h1>Cargando...</h1>
}
</>
  )
}
