import React from 'react';
import {useEffect, useState} from "react";
import Axios from "axios";
import { useNavigate, useParams } from 'react-router-dom';
import { v4 as uuidv4 } from 'uuid';
import {TextField} from "@mui/material"
import Dinamic from "./dinamic"

export default function Comprar() {
  const parametro=useParams();
  const navegar= useNavigate();

  const [files, setfiles] = useState(JSON.parse(localStorage.getItem("files")))
  const [producto, setProducto]=useState({});
  const [estado, setEstado]=useState(false)
  
  const [credenciales, setCredenciales]=useState({
    id_usuario:uuidv4(),
    files:JSON.parse(localStorage.getItem("files")) || {},
    tipo:parametro.type
  })
  const handleChange=(e)=>{
    setCredenciales({...credenciales, [e.target.name]: e.target.value});
  }
  const pagar= async (e)=>{
    e.preventDefault();
    console.log(credenciales)
    const res =  await Axios.post(`/crearPago/${parametro.uuid}`, credenciales);
    if(res.data.verificar === true){
      return console.log(res.data.mensaje)
    }
    const paypal= res.data.links[1].href;
    console.log(paypal)
   return window.location.href = paypal
  }
  const checkProducto= async ()=>{
    const res=await Axios.post("/previaProducto", {uuid:parametro.uuid})
    setProducto(res.data[0]);
    setEstado(true)
  }
  useEffect(() => {
    checkProducto()
  

  }, [])
  
  return (
<>

<form onSubmit={pagar}>
  {
    parametro.type == "ecomers" ? estado ? <ComprarEcomer onChange={handleChange} html={producto.html} css={producto.css} files={files} producto={producto}/>
: <h1>cargando</h1> : parametro.type == "static" ? estado ? <ComprarStatic onChange={handleChange} html={producto.html} css={producto.css} files={files} producto={producto}/> : <h1>Cargando...</h1>: <h1>No se encontro el reultado</h1>
  }
    {parametro.type == "ecomers" || parametro.type == "static"  ? 
    <div className="ancho100 alto10 justificado">
        <button className="btnType1 alto100" type='onSubmit' >Payapal</button>
      </div> : <button onClick={()=>{navegar("/")}} >Regresar a Home</button> } 
  </form>
    </>
  )
}

function ComprarEcomer({onChange, html, css, files, producto}) {
  return (
    <>
    <main className='grid padding30'>
      <div >
      <h1 className="resaltarText">Crea una cuenta de administrador antes de comprar</h1>
    <TextField  inputProps={{style:{fontSize:'1.8rem'}}} InputLabelProps={{style:{fontSize:'1.5rem'}}} sx={{ height:"2.4rem", marginBottom:"4.5rem", '& .MuiOutlinedInput-root':{fontSize:"1.5rem"} }} fullWidth id="fullWidth" margin="dense" label="Nombre de usuario" variant="outlined" name="user_name" onChange={onChange} />
    <TextField  inputProps={{style:{fontSize:'1.8rem'}}} InputLabelProps={{style:{fontSize:'1.5rem'}}} sx={{ height:"2.4rem", marginBottom:"4.5rem", '& .MuiOutlinedInput-root':{fontSize:"1.5rem"} }} fullWidth id="fullWidth" margin="dense" label="Correo de admin" variant="outlined" name="correo_admin" onChange={onChange} />
    <TextField  inputProps={{style:{fontSize:'1.8rem'}}} InputLabelProps={{style:{fontSize:'1.5rem'}}} sx={{ height:"2.4rem", marginBottom:"4.5rem", '& .MuiOutlinedInput-root':{fontSize:"1.5rem"} }} fullWidth id="fullWidth" margin="dense" type="password" label="Contrasea" variant="outlined" name="contrasea" onChange={onChange} />
    <p className='textPequeo' >con esta cuenta podra acceder a la herramienta de administrador de tu pagina para subir tus productos,
        procura no olvidar ni comprartir tus credenciales con nadie...
    </p>
    <p className='textPequeo' >una vez termines de pagar se te otrogara una id unico la cual sera tu llave de acceso para funciones adicionales, como, 
        renovar tiempo de tus servidor, solicitar mantenimiento, cancelar suscripciones, cambiar layout de tu sitio, etc...
    </p>
    <h1 className="resaltarText">Dreccion de envio</h1>
    <TextField inputProps={{style:{fontSize:'1.8rem'}}} InputLabelProps={{style:{fontSize:'1.5rem'}}} sx={{ height:"2.4rem", marginBottom:"4.5rem", '& .MuiOutlinedInput-root':{fontSize:"1.5rem"} }} fullWidth  id="fullWidth" margin="dense" label="Direccion" variant="outlined" name="direccion_envio" onChange={onChange} />
    <p className='textPequeo' >coloca un correo o una red social, a la cual, nosotros te enviaremos el link de tu pagina en cuanto este lista</p>
    <p className='textPequeo' >la entrega de tu sitio sera entre 1 a 12 horas</p>
    <h1 className="resaltarText">credenciales para tu compra</h1>
    <TextField  inputProps={{style:{fontSize:'1.8rem'}}} InputLabelProps={{style:{fontSize:'1.5rem'}}} sx={{ height:"2.4rem", marginBottom:"4.5rem", '& .MuiOutlinedInput-root':{fontSize:"1.5rem"} }} fullWidth  id="fullWidth" margin="dense" label="Nombre" variant="outlined" name="nombre" onChange={onChange} />
    <TextField  inputProps={{style:{fontSize:'1.8rem'}}} InputLabelProps={{style:{fontSize:'1.5rem'}}} sx={{ height:"2.4rem", marginBottom:"4.5rem", '& .MuiOutlinedInput-root':{fontSize:"1.5rem"} }} fullWidth  id="fullWidth" margin="dense" label="Apellido" variant="outlined" name="apellido" onChange={onChange} />
    <TextField  inputProps={{style:{fontSize:'1.8rem'}}} InputLabelProps={{style:{fontSize:'1.5rem'}}} sx={{ height:"2.4rem", marginBottom:"4.5rem", '& .MuiOutlinedInput-root':{fontSize:"1.5rem"} }} fullWidth  id="fullWidth" margin="dense" label="Pais" variant="outlined" name="pais" onChange={onChange} />
    <TextField  inputProps={{style:{fontSize:'1.8rem'}}} InputLabelProps={{style:{fontSize:'1.5rem'}}} sx={{ height:"2.4rem", marginBottom:"4.5rem", '& .MuiOutlinedInput-root':{fontSize:"1.5rem"} }} fullWidth  id="fullWidth" margin="dense" label="Correo personal" variant="outlined" name="correo_personal" onChange={onChange} />
      </div>
      <article className='textType1 sinLineHeigth colorBlack'>
        <div className='ancho100 textPequeo'>
          <h1>Comprando producto</h1>
          <h3>Monto: {producto.precio}$</h3>
          <p className='difuminarTexto'>envio estimado: 12 horas</p>
        </div>
        <div className='textPequeo' >
          <h3 className='resaltarText'>{producto.producto}</h3>
          <article>
            <div>
          <img className='imgGrande' src={producto.img} alt="" />
          <p className="difuminarTexto">{producto.descripcion}</p>
            </div>
            <h1 className="resaltarText textPequeo">Detalles del producto</h1>
            <div className='difuminarTexto'>
          <h1>Precio: {producto.precio}$</h1>
        <p>monto: {producto.tipo}$</p>
        <p>layout: {producto.layout}</p>
        <p>Dominio: {producto.dominio}</p>
        <p>Pasarela de pago: {producto.pasarela_pago}</p>
        <p>Alamacenamientos: {producto.almacenamiento}</p>
        <p>Certificado ssl: {producto.ssl}</p>
        <p>Tecnoligias: {producto.tecnologias}</p>
            </div>
          </article>
        </div>
      </article>
    </main>

        <Dinamic html={html} css={css} files={files} />
    </>
    )
}

