import React from 'react'
import menu from "../icons/menu.png"
import search from "../icons/search.png"
import cart from "../icons/cart.png"

export default function Header() {
  return (<>
          <header className='alto10'>
        <nav className="justificado justificadoBetween"> <div>logotipo</div> <div className='justificado ancho50'><img src={search}  className="imgMediana"/> <img src={cart} className="imgPequea" /> <img src={menu} className="imgPequea" /></div> </nav>
      </header>
      </>
  )
}

export function Nav({title1, title2, descripcion, env1, env2, env3}) {
  return (
    <header className='header gridRow'>
    <nav className='nav2 justificado'><h4 className="resaltarText">{title1}</h4> <p>{descripcion}</p> </nav>
    <nav className='nav3'> <h3 className='resaltarText textGrande'> {title2}</h3> </nav>
    <nav className='nav4 justificado'>
    <p>{env1}</p>
    <p>{env2}</p>
    <p>{env3}</p>
    </nav>
  </header>  )
}

