import React from 'react';
import {useEffect, useState} from "react";
import Axios from "axios";
import { useNavigate } from 'react-router-dom';
import {Nav} from "./components/routes/header"

export default function Inicio() {

  const [productos, setProductos]=useState([])
  
  const navegar= useNavigate()
      const pedirProducto= async ()=>{
        const res=await Axios.get("/productos")
        setProductos(res.data)
        console.log(productos)
        console.log(res)
      }
      useEffect(() => {
        pedirProducto()
        localStorage.clear("files")
      }, [])
      
    
  return (
    <>
    <Nav title1="Comienza a expandir tu negocio ! AHORA vende mas con una pagina y aumenta tus ingresos." title2="Ecomers" descripcion="Simplifica tu trabajo y vende online, aprovecha a comprar tu ecomers hoy mismo y recibelo en pocas ! HORAS " env1="Ecomers" env2="Sitio Web" env3="Pagina web" />
    <div>
      <main className='grid main'>
      {productos.map((data, index)=>{
                return(
    <article className='targetContainer' key={index}>
     <img className='imgGrande' src={data.img} alt="" />
      <div className='infoProducto'>
     <h3 className="resaltarText">{data.producto}</h3>
     <p>{data.descripcion}</p>
     <p>{data.precio + "$"}</p>
     <h3>ver caracteristicas del producto</h3>
     <button onClick={()=>navegar(`/previa/${data.uuid}`)} > ver mas </button>
      </div>
    </article> )})}

      </main>      
    </div>
       
</>
  )
}
